* {
   padding: 0;
   margin: 0;
   border: 0;
   box-sizing: border-box;
}
*,
*:before,
*:after {
   -moz-box-sizing: border-box;
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
}
:focus,
:active {
   outline: none;
}
a:focus,
a:active {
   outline: none;
}
nav,
footer,
header,
aside {
   display: block;
}
html,
body {
   min-width: 320px;
   min-height: 100%;
   height: 100%;
   width: 100%;
   font-size: 100%;
   line-height: 1.5;
   font-size: 14px;
   -ms-text-size-adjust: 100%;
   -moz-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
}

#root {
   width: 100%;
}

button,
textarea {
   font-family: inherit;
}

button {
   cursor: pointer;
}
button::-moz-focus-inner {
   padding: 0;
   border: 0;
}
a,
a:visited {
   text-decoration: none;
}
a:hover {
   text-decoration: none;
}
ul li {
   list-style: none;
}
img {
   vertical-align: top;
}

h3,
h1,
h2 {
   font-weight: 900;
}
p {
   margin: 0;
}
